import React, { PureComponent } from 'react';
import { connect } from 'react-redux';

import { STORE_KEY } from '../../constants';
import { setViewportParams } from '../../actions';

class Viewport extends PureComponent {
  componentDidMount() {
    this.resizeHandler();

    window.addEventListener('resize', this.resizeHandler, false);
  }

  componentWillUnmount() {
    window.removeEventListener('resize', this.resizeHandler, false);
  }

  resizeHandler = () => {
    this.props.dispatch(setViewportParams(this.viewport.offsetHeight));
  };

  render() {
    return (
      <div className="viewport" ref={el => (this.viewport = el)}>
        {this.props.children}
      </div>
    );
  }
}

export default connect(undefined, undefined, undefined, {
  storeKey: STORE_KEY,
})(Viewport);
